@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
.authFormContainer {
  max-width: 320px;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.loginText {
  margin-top: 32px;
  margin-bottom: 12px;
  margin-left: 5px;
  margin-right: 5px;
  font-weight: 700;
  font-size: 31px;
  color: #d9d9d9;
}

.authInput {
  max-width: 320px;
  width: 100%;
  border: 0 solid black;
  border-color: rgb(47, 51, 54);
  border-width: 1px;
  border-radius: 4px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 16px;
  background-color: black;
  color: white;

  font-size: 17px;

  transition: 0.1s;
}

.authInput:focus {
  box-shadow: rgb(29, 161, 242) 0px 0px 0px 1px;
  outline-style: none;
  outline-color: rgb(29, 161, 242);
  border-color: rgb(29, 161, 242);
}

.authSignIn {
  text-align: center;
  width: 330px;
  height: 48px;
  margin-top: 20px;
  border-color: rgba(0, 0, 0, 0);
  border-style: solid;
  border-radius: 9999px;
  outline-style: none;
  background-color: rgb(29, 161, 242);
  cursor: pointer;

  font-size: 15px;
  font-weight: 700;
  color: white;

  transition: 0.2s;
}

.authSignIn:hover {
  background-color: rgb(26, 145, 218);
}

.authInputContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 0em;
}

body {
  background-color: black;
}

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.socialContainer {
  margin-top: 10px;
  max-width: 330px;
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.socialContainer button {
  cursor: pointer;
  border-radius: 9999px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 160px;
  height: 48px;
  background: white;
  cursor: pointer;
  transition: 0.2s;
}

.socialContainer button:hover {
  background-color: lightgray;
}

.formContainer {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding-right: 15px;
}

.pweetFactoryContainer {
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(47, 51, 54);
  width: 100%;
  text-align: center;
}

.mainContainer {
  display: flex;
  flex-direction: row;
  padding-top: 4px;
  padding-bottom: 4px;

  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(47, 51, 54);
}

.pweetFactoryContainer .homeDiv {
  background-color: black;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 3;

  display: flex;
  align-items: center;
  height: 53px;
  font-size: 20px;
  font-weight: 800;
  padding-left: 16px;
  padding-right: 16px;

  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(47, 51, 54);
}

.pweetFactoryContainer .pweetInputContainer {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;

  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(47, 51, 54);
}

.mainContainer img {
  width: 48px;
  height: 48px;
  margin-top: 10px;
  margin-left: 15px;
  border-radius: 9999px;
}

.pweetInputContainer .pweetText {
  margin-left: 10px;
  font-size: 20px;
  font-weight: 400;
  width: 100%;
  height: 56px;
  text-align: start;
}

.inputContainer {
  width: 100%;
  height: 52px;
  display: flex;
  flex-direction: row;
}

.inputContainer .addContainer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-grow: 1;
}

.addContainer label {
  color: rgb(29, 161, 242);
}

.addContainer label:hover {
  cursor: pointer;
}

#addImage {
  display: none;
}

.inputContainer .submitButton {
  align-self: flex-end;

  width: 90px;
  height: 38px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 9999px;
  background-color: rgb(29, 161, 242);

  font-size: 15px;
  font-weight: 700;
  text-align: center;

  transition: 0.2s;
}

.inputContainer .submitButton:hover {
  background-color: rgb(26, 145, 218);
}

.space {
  height: 12px;
  background-color: rgb(21, 24, 28);
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(47, 51, 54);
}

.pweetContainer {
  display: flex;
  padding-top: 5px;
  padding-bottom: 15px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(47, 51, 54);
}

.pweetContainer img {
  width: 48px;
  height: 48px;
  margin-top: 10px;
  margin-left: 15px;
  margin-right: 15px;
  border-radius: 9999px;
}

.pweetContainer .pweetTextContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #cccccc;
  font-size: 15px;
}

.pweetContainer .pweetTextContainer .pweetCreatorContainer {
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: 700;
}

.pweetContainer .pweetTextContainer .pweetCreatorContainer .pweetCreatorUid {
  margin-left: 5px;
  color: rgb(110, 118, 125);
  font-weight: 400;
}

.pweetContainer .pweetTextContainer .pweetButtonContainer {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.pweetContainer .pweetTextContainer .pweetButtonContainer Button {
  display: none;
}

.pweetContainer .pweetTextContainer .pweetButtonContainer label {
  flex-basis: 55.75px;
  cursor: pointer;
  transition: 0.1s;
}

.pweetContainer .pweetTextContainer .pweetButtonContainer label:hover {
  color: red;
}

.pweetContainer .pweetTextContainer .pweetButtonContainer button ~ label:hover {
  color: rgb(26, 145, 218);
}

.routerContainer {
  display: flex;
  width: 100%;
  height: 100%;
}

.routerContainer .item {
  flex-grow: 1;
  flex-basis: 0;
  border-right-color: rgb(47, 51, 54);
  border-right-style: solid;
  border-right-width: 1px;
}

.routerContainer .homeContainer {
  display: flex;
  flex-direction: column;
}

.routerContainer .navContainer {
  display: flex;
  justify-content: flex-end;
}

.navContainer {
  height: 100%;
}

.navContainer ul {
  width: 275px;
  padding-left: 12px;
  padding-right: 12px;
}

.navContainer li {
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  height: 50px;
}

.navContainer li:nth-child(n + 2):hover {
  color: rgb(29, 161, 242);
}

.navContainer .navText {
  margin-left: 20px;
}

.profileContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.profileContainer form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

* {
  box-sizing: border-box;
}

input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

html {
  height: 100%;
}

body {
  height: 100%;
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

#root {
  height: 100%;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

